.menu {
  background-color: $c-gray;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: 32px 0 0;
  }

  &__top {
    display: flex;
    align-items: center;
    align-content: center;
  
    justify-content: space-between;
    padding: 0 72px;
  
    @include onPhone {
      padding: 0 24px;
    }

    &--cross {
      background-image: url(../images/cross.png);
      display: block;
      background-repeat: no-repeat;
      background-position: center;
  
      width: 16px;
      height: 16px;
      text-decoration: none;
  
      @include onDesktop {
        width: 21px;
        height: 21px;
      }
    }

    &--opened {
      margin-bottom: 44px;
    }


  }

  &__list {
    margin: 0 0 40px 0;
    padding: 0 0 0 72px;


    font-weight: 600;
    list-style-type: none;
    color: #1D1D1D;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;

    @include onPhone {
      padding: 0 0 0 20px;
    }
  }

  &__item {
    margin-bottom: 32px;
  }

  &__link {
    text-decoration: none;
    color: #1D1D1D;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    transition: border-color .3s;

    color: #1D1D1D;

    &:hover {
      border-color: #1D1D1D;
    }
  }

  &__call {
    padding: 0 0 0 72px;

    display: block;
    font-weight: 600;
    color: #1D1D1D;
    text-transform: uppercase;

    &--phone {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 27px;
      text-decoration: none;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
    }

    @include onPhone {
      padding: 0 0 0 20px;
    }

  }


}
